* {
  box-sizing: border-box;
}

.App {
  margin-left: auto;
  margin-right: auto;
}

body {
  background-color: #fae5df;
  min-height: 500px;
  font-family: 'Inter', sans-serif;
  margin-bottom: 0;
  padding-bottom: 0;
  color: #141850;
}

.content {
  padding: 5%;
}

.date {
  float: right;
  font-size: smaller;
}

.content ul {
  list-style-type: square;
}

#education, #experience, #skills, #projects {
  padding: 15% 2%;
}

#education h1, #experience h1, #skills h1, #projects h1 {
  margin-bottom: 5%;
}

h4 {
  color: #303179;
  font-size: larger;
}

h1 {
  color: #ed7966;
  line-height: 0.2;
}

.button:hover {
  background-color: rgb(183, 185, 218);
}

.button {
  text-decoration: none;
  background-color: #f5cac2;
  border: 1px solid #303179;
  color: #303179;
  border-radius: 25px;
  padding: 20px; 
  height: 20%;
  width: 20%;
}

@media only screen and (max-width: 731px) {
    .date {
      font-size: 10px;
      position: relative;
      right: -25px;
      writing-mode: vertical-rl;
      text-orientation: mixed;
      letter-spacing: 2px;
      color: #7b7cb3;
   }
}


@keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-moz-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-webkit-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-o-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-ms-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}
